// ------------------- imports
import $ from 'jquery';
import { GLOBAL_VARS } from 'utils/constants';
import { documentReady, pageLoad } from 'utils';
import pageWidgetInit from './dev_vendors/dev_widget';
// ------------------- imports###

// ------------------  import components
import initMap from './components/initMap';
import workspaceCard from './components/workspaceCard';
import Header from './components/Header';
import sliderInit from './components/sliderInit';
import popupInit from './components/popupInit';
import accordion from './components/accordion';
import ajaxLoad from './components/ajaxLoad';
import initInputNumber from './components/inputNumber';
import initAnim from './components/initAnim';
import interactiveMap from './components/interactiveMap';
// ------------------  import components###

window.jQuery = $;
window.$ = $;

// -------------------  dev widget
if (GLOBAL_VARS.projectDevStatus) {
	pageWidgetInit();
	console.log(process.env.NODE_ENV);
}
// -------------------  dev widget###

// -------------------  global variables
// eslint-disable-next-line no-unused-vars

const readyFunc = () => {
	console.log('ready');
	workspaceCard();
};

const loadFunc = () => {
	console.log('page load');

	document.querySelector('body').classList.add('loaded_mod');

	initMap();
	sliderInit();
	popupInit('.popupBtn', '.popupClose', '.popupMember');
	popupInit('.popupAnnouncementOpen', '.popupAnnouncementClose', '.popupAnnouncement');
	popupInit('.popupEmailOpen', '.popupEmailClose', '.popupEmail');
	popupInit('.popupBrochureOpen', '.popupBrochureClose', '.popupBrochure');
	accordion('.accordion_title', '.accordion_content');
	ajaxLoad();
	const initHeader = new Header();
	initInputNumber();
	initAnim();
	interactiveMap();
};

documentReady(() => {
	readyFunc();
});

pageLoad(() => {
	loadFunc();
});
