import slick from 'slick-carousel';
import 'slick-carousel/slick/slick.scss';

import { Fancybox } from '@fancyapps/ui';
import '@fancyapps/ui/dist/fancybox.css';

const sliderInit = () => {
	$('.main_slider__block').on('init reInit afterChange', (event, slider, currentSlide, nextSlide) => {
		let i = (currentSlide || 0) + 1;
		let count = slider.slideCount;
		$('.countCurrent').text(i < 10 ? `0${i}` : `${i}`);
		$('.countTotal').text(count < 10 ? `0${count}` : `${count}`);
		$('.sliderFullscreen').attr('data-fancybox-index', currentSlide);
	});

	$('.main_slider__block').slick({
		prevArrow: $('.main_slider__arrow.prev_mod'),
		nextArrow: $('.main_slider__arrow.next_mod'),
		infinite: false,
		slidesToShow: 1,
		slidesToScroll: 1,
		arrows: true,
		asNavFor: '.preview_slider',
	});

	$('.preview_slider').slick({
		arrows: false,
		dots: false,
		infinite: false,
		vertical: true,
		verticalSwiping: true,
		slidesToShow: 5,
		slidesToScroll: 2,
		asNavFor: '.main_slider__block',
		focusOnSelect: true,
		responsive: [
			{
				breakpoint: 1024,
				settings: {
					slidesToShow: 5,
					vertical: false,
					verticalSwiping: false,
					slidesToScroll: 2,
				},
			},
			{
				breakpoint: 600,
				settings: {
					vertical: false,
					verticalSwiping: false,
					slidesToShow: 3,
					slidesToScroll: 1,
				},
			},
		],
	});

	$('[data-fancybox="gallary_slider"]').on('click', (e) => {
		e.preventDefault();
	});
};

export default sliderInit;
