import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const initAnim = () => {
	console.log('anim !');
	gsap.registerPlugin(ScrollTrigger);

	// fade
	const $fadeEl = document.querySelectorAll('.fadeEl');
	if ($fadeEl) {
		gsap.set($fadeEl, { y: 100, opacity: 0 });
		ScrollTrigger.batch($fadeEl, {
			onEnter: batch => gsap.to(batch, {
				opacity: 1,
				y: 0,
				stagger: 0.3,
				ease: 'none',
			}),
			start: 'top 80%',
		});
	}

	// border
	const $borderEl = document.querySelectorAll('.borderEl');
	if ($borderEl) {
		gsap.set($borderEl, { scaleX: 0, transformOrigin: '0 0' });
		ScrollTrigger.batch($borderEl, {
			onEnter: batch => gsap.to(batch, {
				scaleX: 1,
				stagger: 0.1,
				duration: 0.8,
				ease: 'none',
			}),
			start: 'top 80%',
		});
	}

	// brochure
	const $brochure = document.querySelectorAll('.brochureEl');

	$brochure.forEach((el, i) => {
		const $decor = el.querySelectorAll('.brochureDecor');
		const $content = el.querySelectorAll('.brochureContent');

		const brochureTl = gsap.timeline({
			scrollTrigger: {
				trigger: $brochure,
				start: 'top 60%',
				once: true,
			},
		});

		brochureTl
			.addLabel('start')
			.fromTo($decor, {
				xPercent: gsap.utils.wrap([200, -200]),
				yPercent: gsap.utils.wrap([-200, 200]),
				opacity: 0,
			}, {
				xPercent: 0,
				yPercent: 0,
				opacity: 1,
			}, 'start')
			.fromTo($content, {
				scaleX: 0,
				scaleY: 0,
			}, {
				scaleX: 1,
				scaleY: 1,
			}, 'start+=.2');
	});
};

export default initAnim;
