/*
eslint-disable
	consistent-return,
	object-curly-newline,
	one-var,
	one-var-declaration-per-line,
	no-use-before-define
*/

import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { Loader } from '@googlemaps/js-api-loader';

import { mapStyle } from '../utils/constants';

const interactiveMap = () => {
	const loader = new Loader({
		apiKey: 'AIzaSyBrZNofhuwJ2QPvOjleVt53ytuDRrap5KM',
		version: 'weekly',
	});

	const $map = document.querySelector('.interactiveMap');

	if (!$map) return null;

	gsap.registerPlugin(ScrollTrigger);

	ScrollTrigger.create({
		trigger: $map,
		onEnter: loadMap,
		onEnterBack: loadMap,
		once: true,
	});

	function loadMap() {
		loader
			.load()
			.then((google) => {
				const mapCenterLat = parseFloat($map.dataset.lat);

				if (Number.isNaN(mapCenterLat)) {
					throw new Error('Could not find map center latitude');
				}

				const mapCenterLng = parseFloat($map.dataset.lng);

				if (Number.isNaN(mapCenterLng)) {
					throw new Error('Could not find map center longitude');
				}

				const mapCenterZoom = parseInt($map.dataset.zoom);

				if (Number.isNaN(mapCenterZoom)) {
					throw new Error('Could not find map zoom');
				}

				const mapOptions = {
					center: {
						lat: mapCenterLat,
						lng: mapCenterLng,
					},
					zoom: mapCenterZoom,
					styles: mapStyle,
				};

				const map = new google.maps.Map($map, mapOptions);

				// Markers are generated from locations datasets
				const $locations = Array.from(document.querySelectorAll('.interactiveMapLocation'));

				// Gets markers data
				const markersData = $locations.map($location => {
					const lat = parseFloat($location.dataset.lat);

					if (isNaN(lat)) {
						throw new Error('Could not find location latitude');
					}

					const lng = parseFloat($location.dataset.lng);

					if (isNaN(lng)) {
						throw new Error('Could not find location longitude');
					}

					const markerTheme = Number($location.dataset.markerTheme);

					const markerLabel = String($location.dataset.markerLabel);

					return { lat, lng, markerTheme, markerLabel, $location };
				});

				// Generates markers
				markersData.forEach((markerData) => {
					// Icon styles
					let iconStyles;

					if (markerData.markerTheme === 2) {
						iconStyles = {
							path: google.maps.SymbolPath.CIRCLE,
							fillOpacity: 1,
							fillColor: '#6abeaf',
							strokeWeight: 0,
							scale: 14,
						};
					} else {
						iconStyles = {
							path: google.maps.SymbolPath.CIRCLE,
							fillOpacity: 1,
							fillColor: '#f7c219',
							strokeWeight: 0,
							scale: 12,
						};
					}

					// Label styles
					let labelStyles;

					if (markerData.markerTheme === 2) {
						labelStyles = {
							color: '#ffffff',
							fontSize: '14px',
							fontWeight: '700',
							fontFamily: '"GalanoGrotesque", sans-serif',
						};
					} else {
						labelStyles = {
							color: '#1a2124',
							fontSize: '12px',
							fontWeight: '700',
							fontFamily: '"GalanoGrotesque", sans-serif',
						};
					}

					// Generate marker
					const marker = new google.maps.Marker({
						map,
						position: new google.maps.LatLng(markerData.lat, markerData.lng),
						icon: iconStyles,
						label: {
							text: markerData.markerLabel,
							...labelStyles,
						},
					});

					// Add event listeners
					const { $location } = markerData;
					const activeLocationClassName = 'interactive_map__locations_item--active_mod';

					// `mouseover` and `mouseout` is for the devices that can hover the markers. `click` is for the devices that cannot hover the markers.
					if (window.matchMedia('(hover: hover)').matches) {
						google.maps.event.addListener(marker, 'mouseover', () => {
							$location.classList.add(activeLocationClassName);
						});

						google.maps.event.addListener(marker, 'mouseout', () => {
							$location.classList.remove(activeLocationClassName);
						});
					} else {
						google.maps.event.addListener(marker, 'click', () => {
							markersData.forEach(({ $location: $loc }) => {
								console.log($loc);
								$loc.classList.remove(activeLocationClassName);
							});
							$location.classList.add(activeLocationClassName);
						});
					}
				});
			})
			.catch(error => {
				console.log(error);
			});
	}
};

export default interactiveMap;
