import $ from 'jquery';
import gsap from 'gsap';
import { getWindowSize } from '../utils/index';
import { GLOBAL_VARS } from '../utils/constants';

function workspaceCard() {
	const $cardsArr = document.querySelectorAll('.workspaceCardAnim');

	if ($cardsArr.length) {
		let closeHandler;
		let openHandler;
		const { windowWidth } = getWindowSize();
		const isMobile = windowWidth < GLOBAL_VARS.mediaPoint2;

		if (!isMobile) {
			openHandler = ($inner, $dropdown, offset) => {
				gsap.to($inner, {
					y: -offset,
				});
				gsap.to($dropdown, {
					opacity: 1,
				});
			};

			closeHandler = ($inner, $dropdown) => {
				gsap.to($inner, {
					y: 0,
				});
				gsap.to($dropdown, {
					opacity: 0,
				});
			};
		}

		$cardsArr.forEach((el) => {
			const $inner = el.querySelector('.workspaceCardInner');
			const $dropdown = el.querySelector('.workspaceCardDropdown');

			if (!isMobile) {
				const $content = el.querySelector('.workspaceCardContent');
				let titleHeight;

				if (el.classList.contains('workspace_card--v4_mod')) {
					titleHeight = windowWidth < GLOBAL_VARS.mediaPoint1 ? 75 : 70;
				} else {
					titleHeight = windowWidth < GLOBAL_VARS.mediaPoint1 ? 75 : 125;
				}

				const offset = $content.clientHeight - titleHeight;

				if ($dropdown && offset > 0) {
					$(el).on('focusin mouseenter', () => openHandler($inner, $dropdown, offset));
					$(el).on('focusout mouseleave', () => closeHandler($inner, $dropdown));
				}
			} else {
				el.addEventListener('click', () => {
					$($dropdown).slideDown(300);
				});
			}
		});
	}
}

export default workspaceCard;
