import { Loader } from '@googlemaps/js-api-loader';
import { mapStyle } from '../utils/constants';

const initMap = () => {
	if (document.querySelector('#map')) {
		const loader = new Loader({
			apiKey: document.querySelector('#map').dataset.key,
			version: 'weekly',
			libraries: ['places'],
		});

		const mapOptions = {
			center: {
				lat: parseFloat(document.querySelector('.map_locations__item').dataset.x),
				lng: parseFloat(document.querySelector('.map_locations__item').dataset.y),
			},
			zoom: parseFloat(document.querySelector('#map').dataset.zoom),
			styles: mapStyle,
		};

		let coordX;
		let coordY;
		let infowindow;
		let markersArr = [];
		let i;
		let markerIcon;

		document.querySelectorAll('.map_locations__item').forEach(element => {
			let {
				x,
				y,
				title,
				text,
				link,
			} = (element).dataset;
			let obj = {
				title,
				text,
				x,
				y,
				link,
			};
			markersArr.push(obj);
		});

		loader
			.load()
			.then((google) => {
				let map = new google.maps.Map(document.getElementById('map'), mapOptions);
				markerIcon = {
					url: '/images/marker.svg',
					size: new google.maps.Size(34, 44),
					origin: new google.maps.Point(0, 0),
					anchor: new google.maps.Point(17, 44),
					scaledSize: new google.maps.Size(34, 44),
				};

				document.querySelectorAll('.map_locations__item').forEach(element => {
					element.addEventListener('mouseenter', () => {
						let { x, y } = element.dataset;
						map.setCenter(new google.maps.LatLng(x, y));
						map.setZoom(17);
					});
				});

				infowindow = new google.maps.InfoWindow();

				for (i = 0; i < markersArr.length; i++) {
					const marker = new google.maps.Marker({
						position: new google.maps.LatLng(markersArr[i].x, markersArr[i].y),
						map,
						icon: markerIcon,
						title: markersArr[i].title,
						text: markersArr[i].text,
						link: markersArr[i].link,
					});

					google.maps.event.addListener(marker, 'click', function () {
						infowindow.close();
						infowindow.setContent(`<div class="map_popup"><h3>${this.title}</h3><p>${this.text}</p><a href="${this.link}">View Building</a></div>`);
						infowindow.open(map, marker);
					});
				}
			})
			.catch(e => {
				console.log(e);
			});
	}
};

export default initMap;
