/* eslint-disable */
import $ from 'jquery';

function ajaxLoad() {
	//	hide all content

    $('#load-more').on('click touch', function(){
 
		$.ajax({
			url : cali_loadmore_params.ajaxurl, // AJAX handler
			data : {
				'action': 'loadmorebutton', // the parameter for admin-ajax.php
				'query': cali_loadmore_params.posts, // loop parameters passed by wp_localize_script()
				'page' : cali_loadmore_params.current_page // current page
			},
			type : 'POST',
			beforeSend : function ( xhr ) {
				$('#load-more').text('Loading...'); 
			},
			success : function( posts ){
				if( posts ) {
					$('#load-more').text( 'Load more' );
					$('#load-more-container').append( posts ); // insert new posts
					cali_loadmore_params.current_page++;
 
					if ( cali_loadmore_params.current_page == cali_loadmore_params.max_page ) 
						$('#load-more').hide(); // if last page, HIDE the button
 
				} else {
					$('#load-more').hide(); // if no data, HIDE the button as well
				}
			}
		});
		return false;
	});
 
	/*
	 * Filter
	 */
	$('#sort-order').on('change', function(){
 
        var url = window.location.href;    
        const parser = new URL(window.location);
        parser.searchParams.set('order', $(this).val());
        window.location = parser.href;
 
	});
}

export default ajaxLoad;
