const initInputNumber = () => {
	const inputNumberArray = document.querySelectorAll('.inputNumber');

	if (inputNumberArray.length > 0) {
		inputNumberArray.forEach((item) => {
			const $decrementBtn = item.querySelector('.decrementBtn');
			const $inputNumberElement = item.querySelector('.inputNumberElement');
			const $incrementBtn = item.querySelector('.incrementBtn');
			const dataMin = parseInt($decrementBtn.dataset.min);
			const dataMax = parseInt($incrementBtn.dataset.max);

			// console.log(inputNumberVal, dataMin, dataMax);

			$incrementBtn.addEventListener('click', () => {
				if ($inputNumberElement.value <= dataMax - 1) {
					$inputNumberElement.value++;
					// console.log($inputNumberElement.value);
				}
			});

			$decrementBtn.addEventListener('click', () => {
				if ($inputNumberElement.value >= dataMin + 1) {
					$inputNumberElement.value--;
					// console.log($inputNumberElement.value);
				}
			});
		});
	}
};

export default initInputNumber;
