import gsap from 'gsap';

const popupInit = (popupBtn, popupClose, popup) => {
	const $popupBtn = document.querySelector(popupBtn);
	const $popupClose = document.querySelector(popupClose);
	const $popup = document.querySelector(popup);
	const $body = document.body;

	if ($popupBtn && $popup && $popupClose) {
		console.log('popup: ', $popup.classList[$popup.classList.length - 1]);

		$popupBtn.addEventListener('click', () => {
			$popup.classList.add('popup--active');
			$body.classList.add('body--popup_open_state');

			if ($popup.classList.contains('popup_wide')) {
				const $content = $popup.querySelector('.popupWideContent');

				if ($content) {
					gsap.from($content, {
						opacity: 0,
						duration: 0.6,
					});
				}

				const $bottomLeftAngle = $popup.querySelector('.popupWideAngleBottomLeft');

				if ($bottomLeftAngle) {
					gsap.from($bottomLeftAngle, {
						xPercent: 200,
						yPercent: -200,
						opacity: 0,
						duration: 0.6,
					});
				}

				const $topRightAngle = $popup.querySelector('.popupWideAngleTopRight');

				if ($topRightAngle) {
					gsap.from($topRightAngle, {
						xPercent: -200,
						yPercent: 200,
						opacity: 0,
						duration: 0.6,
					});
				}

				const $img = $popup.querySelector('.popupWideImgWrap');

				if ($img) {
					gsap.from($img, {
						opacity: 0,
						duration: 0.6,
					});
				}
			}
		});

		$popupClose.addEventListener('click', () => {
			$popup.classList.remove('popup--active');
			$body.classList.remove('body--popup_open_state');
		});
	}
};

export default popupInit;
